.article-page {
  .banner {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 500px;
    display: flex;
    .title {
      background-color: rgba(0,0,0,0.5);
      padding: 30px 0 30px 0;
      width: 100%;
      display: flex;
      margin-top: auto;
      h1 {
        color: white;
        margin: 0 0 0 0;
        display: flex;
        line-height: 2em;
        padding-left: 35px; } } }
  .body-wrap {
    margin: 3% 20% 0 20%;
    padding-bottom: 50px;
    .article-body {
      p {
        font-size: 18px;
        text-indent: 30px;
        line-height: 30px;
        span {
          font-weight: 600;
          font-size: 32px; } } }
    .author-info {
      span {
        font-weight: 600;
        font-size: 22px; } } } }
@media screen and ( max-width: $small ) {
  .article-page {
    .body-wrap {
      margin: 3% 10% 0 10%; } } }
